.loading-indicator {
    background: rgb(255 255 255 / .8);
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    transition: opacity .5s ease;
    pointer-events: none;

    &__spinner {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
    }

    &.loading {
        opacity: 1;
    }
}