$purple: #800047;
$blue: #008bca;
$standard-font: Arial, Helvetica, sans-serif;

.App {
  font-family: $standard-font;
  background-color: #282c34;
  min-height: 100vh;
  font-size: 14px;
  color: white;
  overflow: auto;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.blog-entry {
  line-height: 22px;

  &__meta {
    margin-bottom: 30px;

    div {
      margin-top: -10px;
    }
  }

  &__button {
    position: absolute;
    right: 30px;
    top: 37px;
  }

  img {
    max-width: 100%;
  }

  hr {
    border: none;
    background: white;
    height: 2px;
    display: block;
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

/*
  LOGIN CONTAINER
*/

input {
  box-sizing: border-box;
  display: block;
  margin-bottom: 8px;
  border: 0;
  margin-top: 5px;
  outline: none;
  padding: 8px;
  border-radius: 3px;
}

.login-container {
  width: 250px;
  margin: 0 auto;
  background-color: white;
  padding: 30px;
  border-radius: 10px;

  label {
    color: black;
    display: block;
    padding-bottom: 5px;
  }

  input {
    width: 100%;
    border-bottom: 1px solid black;
    border-radius: 0;
  }

  button {
    background-color: transparent;
    color: black;
    border: 1px solid black;
    padding: 10px 20px;
    margin-top: 10px;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.2s ease-out;

    &:hover {
      background-color: #212121;
      color: white;
    }
  }
}

.welcome-headline {
  padding-top: 100px;
  text-align: center;
  padding-bottom: 30px;
  color: white;

  h2 {
    margin: 0;
    padding: 0;
  }
}

.error-message {
  color: red;
  display: block;
  margin-top: 10px;

  &--small {
    font-size: 12px;
  }
}

/*
  MAIN NAVIGATION
*/
.main-navigation {
  padding-bottom: 20px;

  ul {
    display: inline-block;
    padding: 0;
    margin: 0;
    text-align: center;
    width: 100%;
  }

  ul li {
    display: inline-block;

    a,
    button {
      font-size: 14px;
      color: black;
      text-decoration: none;
      padding: 10px;
      margin: 0 20px;
      display: inline-block;
      cursor: pointer;
      font-family: $standard-font;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        height: 2px;
        width: 0;
        margin: auto;
        text-align: center;
        background-color: black;
        transition: width 0.5s ease-out;
      }

      &.active:after,
      &:hover:after {
        width: 100%;
      }
    }

    // &.logout {
    //   position: absolute;
    //   right: 0;
    // }

    button {
      border: 0;
      background: none;
    }
  }
}

.meta-navigation ul {
  display: flex;
  justify-self: flex-end;
}

/*
  MAIN AREA / CONTENT
*/

.main {
  width: 80%;
  margin: 30px auto;
  border-radius: 20px;
  padding: 30px;
  position: relative;
}

/*
  PROFILE PAGE
*/

button {
  background-color: white;
  color: black;
  border: 0;
  padding: 10px 20px;
  margin-top: 10px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.2s ease-out;

  &:hover {
    background-color: gray;
  }

  &.success {
    background: rgb(0 129 0);
    color: white;

    &:hover {
      background: rgb(0 129 0 / .5);
    }
  }
}

.edit-button {
  cursor: pointer;
  border: 0;
  background-color: white;
  border-radius: 100%;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 20px;
}

.weather {
  position: relative;
  width: 100px;

  &-temperature {
    position: absolute;
    top: 3px;
    right: 3px;
    font-size: 20px;
  }
}

header {
  .logo-holder {
    text-align: center;
    
    img {
      max-width: 250px;
    }
  }
}